#boopBlock{
  width: 100%;
  height: 80%;
}

#rangeblock{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
  padding-bottom: 10px;
}

.labelrange{
  color: white;

}

#size{
  width: 400px;
}

#loader{
  border:5px solid;
  width:100px;
  height:100px;
  border-radius:50%;
  border-color:orange transparent transparent;
  animation:spin 0.5s linear infinite;
  position:fixed;
  top:0;
  bottom:0;
  left:0;
  right:0;
  margin:auto;
}

@keyframes spin{
  100%{transform:rotate(360deg);filter:hue-rotate(360deg)}
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  margin: 0;
  background-color: #F9BEE8;
  
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
input[type=range] {
  -webkit-appearance: none;
  width: 100%;
  margin: 8.6px 0;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 13.8px;
  cursor: pointer;
  box-shadow: 1.4px 1.4px 1.3px #4dc2fe, 0px 0px 1.4px #66cbfe;
  background: rgba(0, 0, 0, 0);
  border-radius: 20.4px;
  border: 0px solid #f2d58f;
}
input[type=range]::-webkit-slider-thumb {
  box-shadow: 0px 0px 3.4px #23a397, 0px 0px 0px #28b8aa;
  border: 5px solid #133986;
  height: 31px;
  width: 28px;
  border-radius: 12px;
  background: #b9678b;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -8.6px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: rgba(8, 8, 8, 0);
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 13.8px;
  cursor: pointer;
  box-shadow: 1.4px 1.4px 1.3px #4dc2fe, 0px 0px 1.4px #66cbfe;
  background: rgba(0, 0, 0, 0);
  border-radius: 20.4px;
  border: 0px solid #f2d58f;
}
input[type=range]::-moz-range-thumb {
  box-shadow: 0px 0px 3.4px #23a397, 0px 0px 0px #28b8aa;
  border: 5px solid #133986;
  height: 31px;
  width: 28px;
  border-radius: 12px;
  background: #b9678b;
  cursor: pointer;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 13.8px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: rgba(0, 0, 0, 0);
  border: 0px solid #f2d58f;
  border-radius: 40.8px;
  box-shadow: 1.4px 1.4px 1.3px #4dc2fe, 0px 0px 1.4px #66cbfe;
}
input[type=range]::-ms-fill-upper {
  background: rgba(0, 0, 0, 0);
  border: 0px solid #f2d58f;
  border-radius: 40.8px;
  box-shadow: 1.4px 1.4px 1.3px #4dc2fe, 0px 0px 1.4px #66cbfe;
}
input[type=range]::-ms-thumb {
  box-shadow: 0px 0px 3.4px #23a397, 0px 0px 0px #28b8aa;
  border: 5px solid #133986;
  height: 31px;
  width: 28px;
  border-radius: 12px;
  background: #b9678b;
  cursor: pointer;
  height: 13.8px;
}
input[type=range]:focus::-ms-fill-lower {
  background: rgba(0, 0, 0, 0);
}
input[type=range]:focus::-ms-fill-upper {
  background: rgba(8, 8, 8, 0);
}
